.header {
    z-index: 10;
  }
  
  .header-link {
    color: #000000;
    font-weight: 600;
    position: relative;
    transition: color 0.2s;
  }
  
  .header-link::before {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #000000;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
  
  
  .header-link:hover::before {
    visibility: visible;
    width: 100%;
  }
  